import { createApp } from 'vue';
import App from './App.vue';
import * as BoidsModule from './wasm/build/wasm_boids.js';

let wasmModule = null;

BoidsModule.default({
    locateFile: (path) => {
        if (path.endsWith('.wasm')) {
            //  '/wasm-boids/' 
            return (process.env.NODE_ENV === 'production' ? '/wasm-boids' : '') + `/static/js/${path}`;
        }
        return path;
    },
}).then(Module => {
    wasmModule = Module;
    console.log('Wasm module initialized:', Module);

    // Vue  WebAssembly 
    const app = createApp(App);
    app.provide('wasmModule', Module); // provide 
    app.mount('#app');
});